//
// WHERE CLAUSES HERE
//
const countableFields = { 'Programs': true, 'Super Regions': true, 'Persona': true, 'Industry': true, 'Company Size': true, 'Engagement Status': true, 'Installed Technologies': true, 'Campaign Type': true };
const uncountableFields = { 'Countries': 'Country', 'Metro Areas': 'MetroArea', 'Companies': 'Companies' };
const getWhereClause = (filters) => {
    const keys = Object.keys(filters).filter(k => filters[k].length);
    const whereCountable = keys
        .filter(k => countableFields[k])
        .map(k => {
            const thisArr = filters[k];
            return `(${thisArr
                .map(val => `\`${val}\`>0`)
                .join(' OR ')})`;
        });
    const whereUncountable = keys
        .filter(k => !countableFields[k])
        .map(k => {
            const thisArr = filters[k];
            const name = uncountableFields[k] || k;
            return `\`${name}\` IN (${thisArr.map(sqlstring.escape).join(', ')})`;
        });
    const where = whereCountable.concat(whereUncountable);
    if (where.length) {
        return `WHERE ${where.join(' AND ')}`;
    }
    return '';
};

